 class=\"center\">\n\t\t\t\t<div class=\"clear\">\n\t\t\t\t\t<div class=\"az f-l\" :class=\"letter==item?'color-409EFF':''\" @click=\"getLetter($event,item)\" v-for=\"item in AZ\">{{item}}</div>\n\t\t\t\t</div>\n\t\t\t\t<hr v-if=\"brand.length>0\">\n\t\t\t\t<div v-if=\"brand.length>0\" style=\"margin: 10px 0;\">\n\t\t\t\t\t<el-button size=\"mini\" @contextmenu.native.prevent=\"delBrand(item.id,item.name)\" @click=\"getBid(item.id)\" v-for=\"item in brand\" :key=\"item.id\">{{item.name}}\n\t\t\t\t\t</el-button>\n\t\t\t\t</div>\n\t\t\t\t<hr v-if=\"letter||bid\">\n\t\t\t\t<div style=\"padding: 10px 0;\">\n\t\t\t\t\t<el-button @click=\"addBrand\" v-if=\"letter\" type=\"primary\">车辆品牌添加</el-button>\n\t\t\t\t\t<el-button @click=\"addCarType\" v-if=\"bid\" type=\"primary\">车辆型号添加</el-button>\n\t\t\t\t\t<el-button @click=\"changeBrand\" v-if=\"bid\" type=\"primary\">品牌修改</el-button>\n\t\t\t\t</div>\n\t\t\t\t<hr v-if=\"carList.length>0\">\n\t\t\t\t<table v-if=\"carList.length>0\">\n\t\t\t\t\t<tr>\n\t\t\t\t\t\t<th>车型</th>\n\t\t\t\t\t\t<th>排量</th>\n\t\t\t\t\t\t<th>用油</th>\n\t\t\t\t\t\t<th>升数(L)</th>\n\t\t\t\t\t\t<th>保养周期(月)</th>\n\t\t\t\t\t\t<th>保养公里数(KM)</th>\n\t\t\t\t\t\t<th>价格(元)</th>\n\t\t\t\t\t\t<th>滤芯补助(元)</th>\n\t\t\t\t\t\t<th>操作</th>\n\t\t\t\t\t</tr>\n\t\t\t\t\t<tr v-for=\"item in carList\" :key=\"item.id\">\n\t\t\t\t\t\t<td>{{item.type}}</td>\n\t\t\t\t\t\t<td>{{item.series}}</td>\n\t\t\t\t\t\t<td>{{item.oil}}</td>\n\t\t\t\t\t\t<td>{{item.litre}}</td>\n\t\t\t\t\t\t<td>{{item.month}}</td>\n\t\t\t\t\t\t<td>{{item.km}}</td>\n\t\t\t\t\t\t<td>{{item.price}}</td>\n\t\t\t\t\t\t<td>{{item.filter}}</td>\n\t\t\t\t\t\t<td>\n\t\t\t\t\t\t\t<el-button type=\"text\" @click=\"changeCarType(item.id)\">修改</el-button>\n\t\t\t\t\t\t\t<el-button style=\"color:#EC5F63\" @click=\"delCarType(item.id)\" type=\"text\">删除</el-button>\n\t\t\t\t\t\t</td>\n\t\t\t\t\t</tr>\n\t\t\t\t</table>\n\t\t\t\t<div class=\"page_center\" v-show=\"totalPage&&totalPage>1&&carList&&carList.length>1\">\n\t\t\t\t\t<